.Leaderboard{
    position:relative;
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 95%;
    padding: 10px;
    border-radius: 10px;
    height: 65vh;
    background-color: #264653;
 
}
.Leaderboard-main-heading{
    display: flex;
    width: 100%;
    gap: 10px;
    justify-content: space-around;
    background-color: #f4a361;
    border-radius: 8px;
    h2{
        margin: 0; 
    padding: 5px;
    font-weight:600;
    }
    button{
        flex-basis: fit-content;
    }
    .back-button{
        position: static;
        margin: 0;
        
    }
}
.Leaderboard-heading-container h4{
    background-color: #f4a361;
    padding: 5px;
    border-radius: 5px;
    font-weight:600;
}
.Leaderboard-heading-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 5px;
    flex-basis: 1;
    margin: 0;
}
.Leaderboard-headings{
    display: grid;
    width: 100%;
    grid-template-columns: 21.5% 52.3% 26.2%;

    background-color: #f4a361;
    border-radius: 5px;
}
.Leaderboard-headings h5{
    width: inherit;
    border: 2px solid #264653;
}
.timeline-buttons{
    display: flex;
    justify-content: space-between;
    width: 100%;
    gap: 5px;    
}
.timeline-buttons-button{
    background-color: #f4a361;
    display: grid;
    width: 33%;
    gap: 0;
    height: fit-content;
    grid-template-columns: repeat(10,10%);
    grid-template-rows: repeat(12,5px);
    transition: all 0.7s ease;
    padding: 0;
    border: 1px solid #264653;
    overflow:hidden;
    p{
        grid-column: 1/8;
        grid-row: 1/6;
    
    }
  
    .championship{
        grid-column: 1/8;
        grid-row: 6/9;
        font-weight: 100;
        font-size: 10px;
        
    }
    .ends{
        font-size: 14px;
        grid-column: 1/-1;
        width: 100%;
        height: fit-content;
        grid-row: 9/-1;
        background-color: #e76f51;
    
        
    }
    svg{
        grid-column: 8/-1;

        grid-row: 1/9;
        background-color: #e76f51;
       
     
    }
   
}
.goat{
    p{
        grid-column: 1/8;
        grid-row: 1/9;
    
    }
}
.timeline-buttons-button:hover{
    background-color: #264653;
    border: 1px solid #f4a361;
    p,.championship{
        color:#f4a361;
    }
    svg{
        background-color: #e76f51b4;
    }
   
}
.loader{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top:50px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 4px solid #264653; /* Light grey */
    border-top: 4px solid #e76f51; /* Blue */
    animation: spin 2s infinite;
}
@keyframes spin {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
    
}
.Leaderboard-winner{
    background-color:#e76f51;
    padding:3px 10px;
    border-radius: 4px;
}
.Leaderboard-data-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 5px;
    flex-basis: 1;
    margin: 0;
    overflow-y: auto;
    scrollbar-width:none;
    
}
.Leaderboard-data{
    display: grid;
    width: 95%;
    border-radius: 5px;
    background-color: #f4a361;
    grid-template-columns: 20% 55% 25%;
    grid-template-rows: fit-content;
    background-color:#d9d9d95b ;
    p{
        width: 100%;
        height: 100%;
        border: 2px solid #264653;
        font-size: 22px;
        color: #c9cba3;
    }
}




.first p{
    font-size: 30px;
    color: #f6ae2d;
    svg{
        height: 38px;
    }
}

.second p{
    font-size: 28px;
    padding-top: 0.5px;
    color: #d3d3d3;
    svg{
        height: 33px;
    }
}

.third p{
    padding-top: 0.5px;
    font-size: 26px;
    color: #fe7f2d;
    svg{
        height: 28px;
        margin: 2px;
    }
}
@media screen and (max-width:800px){

    .Leaderboard-data{
        p{
            font-size: 19px;
        }
    }
    .first p{
        font-size: 26px;
        svg{
            height: 34px;
        }
    }
    .Leaderboard-main-heading{
        gap: 0px;
        button{
            svg{
                width: 27px;
            }
        }
        h2{
            font-size: 25px;
        }
        svg{
            width: 30px;
        }

    }
    
    .second p{
        font-size: 23px;
        svg{
            height: 26px;
        }
    }
    
    .third p{
        font-size: 21px;
        svg{
            height: 24px;
        }
       
    }
    .timeline-buttons-button
{
    grid-template-rows: repeat(12,3.7px);
        p{
            font-size: 14px;
            align-self: baseline;
        }
        .championship{
            font-size: 7px;
        }
        svg{
            height:30px;
            width: 100%;
        }
        .ends{
            font-size: 9px;
            font-weight:100;
        }
    } 
    .Leaderboard-winner{
        padding:1.5px 5px;
        border-radius: 4px;
        font-size: 10.5px;
        svg{
            height:29px;
            width: 29px;
        }
    }
    }