
.cover h1{
    margin: 2%;
}
#name{
    display: block;
    border: none;
    margin-top:20px ;
    margin-bottom: 15px;
    padding: 2px;
    font-size: 30.8px;
    border-radius: 5px;
   width: 70%;
   text-align:center;
}

.leaderboard-button{
   display: block;
   margin: 15px auto 10px;
}
#name::placeholder{
   text-align: center;
   font-size: 16.8px;
}
#name:focus{
outline: none;
}
.cover{
    border: 0.2px solid #264653;
    margin: auto;
    display:inline-block;
    min-width:484.533px;  
    max-width:484.533px;   
    background-color:#e76f51;
    border-radius:20px;
    box-shadow: 10px 8px 50px -5px #515151;
    position: absolute;
    top: 50%;
  left: 50%;
 
  transform: translate(-50%, -53%);
}
.cover-down{
   padding-bottom:18px ;
}
.title h1{
    font-size:63px;
    color: #1e363f;
    font-family: 'Russo One', sans-serif;

}

.dots{

   display: inline-block;
   height: 100%;
   text-align: center;
   
}
.dots span{
   animation: fade 3s cubic-bezier(0, 0.5, 0.5, 1)  infinite;
}
.dots span:nth-child(1){
   animation-delay: -2s;
}
.dots span:nth-child(2){
   animation-delay: -1.3333s;
}
.dots span:nth-child(3){
   animation-delay: -0.6666s;
}

@keyframes fade {
   0%{
      opacity: 100;
   }
   50%{
      opacity: 70;
   }
   100%{
      opacity:0;
   }
}
.button{
    border: 1px solid  #264653;
 background-color: #ff8e32;
 border-radius: 5px;
 cursor: pointer;
 padding: 0;
 }
 
 .button-span{
    border: 2px solid  #264653;
    cursor: pointer;
    padding: 12px 42px;
    border-radius: 5px;
    background-color: #f4a361;
    display: inline-block;
    transform: translateY(-9px);
    transition:transform 200ms cubic-bezier(.3, .7, .4, 1);
    font-size: 1.75rem;
    
 }
 
 .button-span:hover{
    transform: translateY(-11px);
 }
 .button-span:active{
    transform: translateY(-3px);
 }
 .disable{
    pointer-events: none;
 }

